/* http://meyerweb.com/eric/tools/css/reset/ */
/* v1.0 | 20080212 */

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, font, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

/* remember to define focus styles! */
:focus {
  outline: 0;
}

/* remember to highlight inserts somehow! */
ins {
  text-decoration: none;
}
del {
  text-decoration: line-through;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.table {
  width: 100%;
  margin: 25px auto 0;
  overflow-x: auto;
}
.table table,
.table th,
.table td {
  width: 100%;
  margin: 0 auto;
  padding: 3px 5px;
  text-align: center;
  border-collapse: collapse;
  border: 1px solid black;
}
.table .headers {
  background: white;
}
.table td {
  cursor: default;
}
.table tr td:first-child {
  background: white;
  text-shadow: 2px 3px 10px antiquewhite;
}
.table .checked {
  background: #009200;
}
.table .in-progress {
  background: #fff55e;
}
.table .checking {
  background: #ff3816;
}
.table .unchecked {
  background: darkred;
}
.table .todo {
  background: grey;
}
