@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 300;
  src: local('Roboto Light'), local('Roboto-Light'), url(https://fonts.gstatic.com/s/roboto/v18/KFOlCnqEu92Fr1MmSU5fBBc9.ttf) format('truetype');
}
.login-page {
  width: 360px;
  margin: auto;
  padding: 8% 0 0;
}
.login-page .form {
  position: relative;
  z-index: 1;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  background: #FFFFFF;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.login-page .form input {
  width: 100%;
  margin: 5px 0;
  padding: 15px;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  outline: 0;
  background: #f2f2f2;
  border: 0;
  box-sizing: border-box;
}
.login-page .form button {
  width: 100%;
  margin: 5px 0 0;
  padding: 15px;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  background: #4CAF50;
  border: 0;
  color: #FFFFFF;
  outline: 0;
  transition: all 0.3s ease;
  cursor: pointer;
}
.login-page .form button:hover,
.login-page .form button:active,
.login-page .form button:focus {
  background: #43A047;
}
.login-page .form .message {
  margin: 15px 0 5px;
  font-size: 12px;
  color: #b3b3b3;
}
.login-page .form .message a {
  color: #4CAF50;
  text-decoration: none;
}
.login-page .form .register-form,
.login-page .form .login-form {
  overflow: hidden;
  transition: 0.5s opacity, 0.5s height, 0.5s -webkit-transform;
  transition: 0.5s opacity, 0.5s height, 0.5s transform;
  transition: 0.5s opacity, 0.5s height, 0.5s transform, 0.5s -webkit-transform;
}
.login-page .form .register-form.-hide,
.login-page .form .login-form.-hide {
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
  height: 0;
  opacity: 0;
}
.login-page .form .warning {
  margin: 5px 0;
  color: red;
  font-size: 12px;
}
html {
  height: 100%;
}
body {
  background: url(/static/media/581559031.a9224ae6.jpg) no-repeat center center;
  background-size: cover;
}
